<template>
	<yd-layout >
    	<yd-navbar slot="navbar" title="启动流程">
      		<a href="javascript:;" @click="goBack" slot="left">
        	<yd-navbar-back-icon></yd-navbar-back-icon>
      	</a>
    </yd-navbar>
    
    <yd-cell-group>
      	
        <yd-cell-item  type="label">
          <select slot="right" @change="changeOpinion">
            <option value="">选择</option>
            <option value="同意">同意</option>
            <option value="不同意">不同意</option>
          </select>
        </yd-cell-item>
        <yd-cell-item>
          <yd-textarea slot="right" placeholder="请输入意见" maxlength="200" :value="opinion"></yd-textarea>
        </yd-cell-item>
        <yd-cell-item type="label">
          <rx-attachment  :readonly="false" slot="right" v-model="files"></rx-attachment>
        </yd-cell-item>
        
    </yd-cell-group>
    
    
    
    <yd-tabbar slot="tabbar">
		<tabbar-item title="确定" name="approve" type="a" v-on:handclick="startFlow()" icon="yd-icon-approve"
                   :active="true"></tabbar-item>
        <tabbar-item title="取消" name="cancel" type="a" v-on:handclick="cancel()" icon="yd-icon-cancel" ></tabbar-item>
    </yd-tabbar>

  
  </yd-layout>
</template>

<script>
	
import Vue from 'vue'


import rxboxs from '@/components/common/rx-boxs';
import RxAttachment from '@/components/common/form/RxAttachment';
import router from '@/router/index';
import {RxUtil} from "@/assets/util.js";
import { convertBoJson } from '@/assets/app.js';

Vue.component(RxAttachment.name, RxAttachment);

export default{
    name : "rx-startflow",
    data(){
      return {
        	opinion:"同意",
	        files:"[]",
	        bpmSolution:{},
	        instId:"",
	        isSinglePage:true
    	}
    },
    mounted() {
       this.isSinglePage = this.$route.params.isSinglePage;
	   var data= this.$store.state.bpmModule.instData;
	   console.info("startflow activated:" + data)
	   console.info(data)
	   this.bpmSolution=data.bpmSolution;
	   if(!data.instId){
	   	this.instId=data.instId;
	   }
	   
	},
	methods: {
      changeOpinion(e){
        var v=e.srcElement.value;
        this.opinion=v;
      },
      /**
       * 构建cmd数据。
       */
      getProcessCmd(){
      	var bpmSolution=this.bpmSolution;
      	var approveCmd={};
	   
	    approveCmd.solId=bpmSolution.solId;
	    approveCmd.opinion=this.opinion;
	    approveCmd.isValid=true;
	    approveCmd.message="success";
	    
	    if(this.instId){
	    	approveCmd.bpmInstId=this.instId;
	    }
	    
	    var data= this.$store.state.bpmModule.instData.formModels;
	    var jsonData=convertBoJson(data);
	    
	   
	    var json= JSON.stringify(jsonData);
	    json=json.replaceAll("&nbsp;","");
	   
	    
	    approveCmd.jsonData=json;
	    
	    approveCmd.opFiles=this.files;
	    
	    return approveCmd;
      },
      
      cancel(){
      	router.push({ name: 'startForm', params: { solId:this.bpmSolution.solId,instId:this.instId?this.instId:"-1" }})
      },
      startFlow(){
	      var approveCmd=this.getProcessCmd();
	      //获取数据失败。
	      if(!approveCmd.isValid){
	      	this.errorToast(approveCmd.message)
	      	return;
	      }
	      delete approveCmd.isValid;
	      delete approveCmd.message;
	      
	      var url=_baseUrl +"/bpm/core/bpmInst/startProcess.do";
	      var params=RxUtil.serialJsonToForm(approveCmd);
		  this.$dialog.loading.open('正在启动流程,请稍候...');
	      this.$ajax.post(url,params).then(response =>{
	        this.$dialog.loading.close();
	        var data=response.data;
	        if(data.success){
	        	this.$store.commit('cleanInstData');
	        	if(this.isSinglePage){
	        		this.successToast("提交成功！",1000,'success');
	        	}else{
	        		this.successToast("提交成功！",1000,'mySolutions');
	        	}
	        	
	        }
	        else{
	        	this.errorToast("操作失败！")
	        }
	      })
      }
	}
}	


</script>

<style scoped>
	.yd-flexbox-item-center{
		text-align: center;
		margin-top: 2px;
		padding:0px 5px 0px 5px;
	}
	
	.yd-radio-text{
	    overflow: hidden;
	    text-overflow: ellipsis;
	    white-space: nowrap;
	    width: 2.5rem;
	   
	    display: inline-block;
	}
	
	
	.btn-select{
    	display: table-cell;height: 44px;vertical-align: middle;
  	}

	.cell-left{
	    width: 4rem;
	    height: 1.55rem;
	    overflow-x: auto;
	}
	
	
</style>


