<template>
	<yd-flexbox>
            <yd-flexbox-item>
            	<div  class="div-container">
			        <div 
			        	v-for="(item,index) in list" 
			        	class="item" 
			        	:class="{'item-selected':item.selected}" 
			        	@click="selectItem(item)"
		        	>
		        		{{item.name}}
			        	<i 
			        		class="i-remove yd-icon-remove" 
			        		v-if="item.selected" 
			        		@click.stop="removeSelect(index)" 
		        		></i>
			        </div>
			    </div>
            </yd-flexbox-item>
            <div class="btn-select">
            	<yd-icon 
            		slot="icon" 
            		v-show="allowSelect" 
            		name="add-user" 
            		@click.native="handSelect()" 
            		color="#177cb0"
        		></yd-icon>
            </div>
    </yd-flexbox>
</template>

<script type="text/ecmascript-6">

import {RxUtil} from "@/assets/util.js";

export default{
    name : "rx-boxs",
    props: {
    	value:String,
   		textField:String,
        valField:String,
        containstyle:String,
        allowSelect:{
		    type: Boolean,
		    default: true
		},
		single:{
			type: Boolean,
		    default: false
		}
	},      
	data(){
    	return {
    		list:[]//this.$store.state.pageModule.fromUser
    	}
   },
	activated(){
		this.resetData();
	},
 	mounted() {
 		//将数据放到data的list中。
 		//[{id:"",text:""},{id:"",text:""}]
	    this.setValue(this.value);
	},
	methods: {
		resetData(){
			this.list = [];
			console.log(15415415314653)
		},
	    removeSelect(index){
	    	this.list.splice(index,1);
	    	var v=this.getValue();
	    	this.$emit('input', v);
	    },
	    selectItem(item){
	    	item.selected=!item.selected;
	    },
	    setValue(value){
		    if(!value) return;
		    //value 格式  [{id:"",text:""},{id:"",text:""}]
		    this.render(value);
		    var v=this.getValue();
		    this.$emit('input', v);
	    },
	    render(value){
	    	//value 格式  [{id:"",text:""},{id:"",text:""}]
	    	if(this.single){//单选  清空原来选的值
	    		this.list=[];
	    	}
		    var json=eval("(" + value +")");
		    for(var i=0;i<json.length;i++){
		    	var obj=json[i];
		    	var rtn=RxUtil.isInList(this.list,"id",obj[this.valField]);
		    	if(rtn) continue;
		    	
		    	var o={};
		    	o.id=obj[this.valField];
		    	o.name=obj[this.textField];
		    	o.selected=false;
		    	this.list.push(o);
		    }
	    },
	    /**
	     * 取得选择的ID值。
	     * isAry:
	     * true 返回数组
	     * false 返回逗号分割的字符串
	     */
	    getIdVal(isAry){
	    	return this.getSingleVal(isAry,true);
	    },
	    /**
	     * 取得选择的文本值。
	     * isAry:
	     * true 返回数组
	     * false 返回逗号分割的字符串
	     */
	    getTextVal(){
	    	return this.getSingleVal(isAry,false);
	    },
	    getSingleVal(isAry,isId){
	    	if(this.list.length==0){
	    		return isAry?[]:"";
	    	}
	    	var jsonAry=[];
	    	for(var i=0;i<this.list.length;i++){
	    		var obj=this.list[i];
	    		var v=isId?obj.id:obj.name;
	    		jsonAry.push(v);
	    	}
	    	return isAry?jsonAry:jsonAry.join(",");
	    },
	    getValue(){
	    	if(this.list.length==0) return "[]";
	    	var jsonAry=[];
	    	for(var i=0;i<this.list.length;i++){
	    		var o={};
	    		var obj=this.list[i];
	    		o[this.textField]=obj.name;
	    		o[this.valField]=obj.id;
	    		jsonAry.push(o);
	    	}
	    	return JSON.stringify(jsonAry);
	    },
	    handSelect(){
	    	this.$emit('select',this);
	    }
	},
	watch: {
		value: function (val, oldVal){
			this.render(val);
		}
	}
}
</script>

<style scoped>
	
	.div-container{
		box-sizing: border-box;
		width: 100%;
        max-width: 6rem;
		height: .7rem;
		display: flex;
        align-items: center;
        overflow-x:auto;
        overflow-y:hidden; 
	}
	
	.div-container > .item{
		margin: 0 0.1rem;
		display: flex;
		border-radius: .2rem;
		padding: 0.05rem 0.15rem;
		position: relative;
		box-shadow: 0.02rem 0.02rem 0.05rem #888;
		background-color: #dee7f8;
		height: .4rem;
		white-space:nowrap;  
		align-items: center; 
        justify-content: center; 
        width:auto;
	}
	
	.div-container > .item-selected{
		background-color: rgba(120, 193, 245, 0.4);
		color: #fff;
	}
	
	
	.div-container > .item > .i-remove{
		position: absolute;
		top: 0px;
		right: 0px;
		cursor: hand;
	}
	
	.btn-select{
		width: auto;
		margin-right:.12rem;
		display: flex;
        align-items: center; /*定义body的元素垂直居中*/
        justify-content: center; /*定义body的里的元素水平居中*/
	}
	
	
	.yd-icon-remove:before { 
		font-size: 0.3rem !important;
		color: #000;
		font-weight: bold;
	}
	
	.yd-icon-add-user:before{
		font-size: 0.5rem;
	}
</style>


