<template>
	<div class="attacment" v-if="right=='r'">
		<div v-for="(file,index) in files" class="file-container" >
            <yd-lightbox>
		        <yd-lightbox-img  :src="imageUrl(file)"></yd-lightbox-img>
		    </yd-lightbox>
        </div>
	</div>
	<div class="attacment" v-else-if="right=='w'">
		<div class="div-left" >
            <div v-for="(file,index) in files" class="file-container" >
              <div v-if="file.fileName.endsWith('mp4')">
	              <i class="file-remove yd-icon-removefile" v-if="file.selected" @click="removeSelect(index)" ></i>
	              <yd-icon slot="icon" name="video" color="#177cb0" @click="checkFile(index)"></yd-icon>
	              
              </div>	
              <div v-else>
              	  <i class="file-remove yd-icon-removefile" v-if="file.selected" @click="removeSelect(index)" ></i>
	              <img :src="imageUrl(file)" class="file" @click="checkFile(index)"     />             	
              </div>
              
            </div>
        </div>
        <div class="div-right">
            <i class="yd-icon-add" @click="showSelect=true;"></i>
        </div>
        <yd-actionsheet :items="operators" v-model="showSelect" cancel="取消"></yd-actionsheet>
    </div>
</template>

<script>
import { calcRight } from "@/assets/app.js";
import wx from 'weixin-js-sdk';

export default{
    name : "rx-attachment",
    data(){
    	return {
    		files:[],
    		showSelect:false,
    		operators: [
    			{
	                label: '拍照',
	                method: () => {
	                    this.wxPhoto();
	                }
	            },{
	                label: '从相册中选择',
	                method: () => {
	                    this.wxSelectPhoto();
	                }
	            }
	        ]
    	}
    },
    props: {
    	value:String,
    	permission:Object,
    	permissionkey:String,
    	readonly: {
            type: Boolean,
            default: false
        }
    },computed: {
    	right :function () {
    		return calcRight(this);
    	}
    },
    methods:{
    	imageUrl(file){
    		return _baseUrl +"/sys/core/file/download/"+file.fileId+".do"
    	},
    	parseUrl(file){
    		if(file.fileName)
    		return _baseUrl +"/sys/core/file/download/"+file.fileId+".do"
    	},
    	
    	handFiles(json){
	        if(!json.success) return [];
	        console.info("handFiles-----------");
	        var aryFiles=json.data;
	        for(var i=0;i<aryFiles.length;i++){
	          var file=aryFiles[i];
	          this.files.push({fileId:file.fileId,fileName:file.fileName,selected:false});
	        }
      	},
      	
      	handFile(json){
      		this.files.push({fileId:json.key,fileName:json.val,selected:false});
      	},
      	
      	/**
      	 * 删除选测图片
      	 */
    	removeSelect(idx){
	        this.files.splice(idx, 1);
	    },
	    /**
	     * 选中图片。
	     */
	    checkFile(idx){
	        var file=this.files[idx];
	        file.selected=!file.selected;
	    },
	    /**
	     * 选择相册中的图片。
	     */
	    selectAttch(){
        	var _self=this;
	        plus.gallery.pick( function(e){
	        	var fileAry=[];
	            for(var i=0;i< e.files.length;i++){
	              var path= e.files[i];
	              
	              fileAry.push(path);
	            }
	            _self.upload(fileAry);
	          }, function ( e ) {
	          },{filter:"image",multiple:true});
	    },
	    wxPhoto(){
	    	this.wxImage("camera");
	    },
	    /**
	     * 1.选择图片
	     * 2.上传到微信服务器
	     * 3.根据上传的文件ID,下载到jsaas服务器。
	     */
	    wxSelectPhoto(){
	    	
	    	this.wxImage("album");
	    },
	    wxImage(type){
	    	
	    	var self_=this;
	    	wx.chooseImage({
			    sizeType: ['compressed'], // 可以指定是原图还是压缩图，默认二者都有
			    sourceType: [type], // 可以指定来源是相册还是相机，默认二者都有
			    success: function (res) {
			    	var localIds = res.localIds; // 返回选定照片的本地ID列表，localId可以作为img标签的src属性显示图片
			       	for(var i=0;i<localIds.length;i++){
			    		wx.uploadImage({
						    localId: localIds[i], // 需要上传的图片的本地ID，由chooseImage接口获得
						    isShowProgressTips: 1, // 默认为1，显示进度提示
						    success: function (res) {
						        var serverId = res.serverId; // 返回图片的服务器端ID
						        var url=_baseUrl +"/wx/saveFile.do";
						        var params="mediaId=" + serverId;
								self_.$ajax.post(url,params).then(res=>{
									var data=res.data;
									self_.handFile(data);
								});
							}
						});
					}
			    }
			});
	    },
	    /**
	     * 拍照
	     */
      	takePhoto(){
        	var _self=this;
        	var cmr = plus.camera.getCamera();
        	var fmt = cmr.supportedImageFormats[0];
        	cmr.captureImage( function( path ){
            	plus.io.resolveLocalFileSystemURL(path, function(entry) {
               		var tmpPath= entry.toLocalURL();
               		var fileAry=[];
               		fileAry.push(tmpPath);
               		_self.compressImg(fileAry);
            	})
          	},
          	function( error ) {},
          	{format:fmt});
      	},
      	recordVideo(){
      		var _self=this;
      		var cmr = plus.camera.getCamera();
			var res = cmr.supportedVideoResolutions[0];
			var fmt = cmr.supportedVideoFormats[0];
			
			cmr.startVideoCapture( function( path ){
					plus.io.resolveLocalFileSystemURL(path, function(entry) {
	               		var tmpPath= entry.toLocalURL();
	               		var fileAry=[];
	               		fileAry.push(tmpPath);
	               		_self.upload(fileAry);
	            	});
				},
				function( error ) {},
				{resolution:res,format:fmt}
			);
      	},
      	/**
      	 * 上传文件。
      	 */
      	upload(fileAry){
       		
       		var self_ = this;
      		this.$dialog.loading.open('正在上传...');
      		var url=_baseUrl +"/sys/core/file/upload.do";
      		var task = plus.uploader.createUpload(url, { },function ( t, status ) {
	            self_.$dialog.loading.close();
	            // 上传完成
	            if ( status == 200 ) {
	              var json=JSON.parse( t.responseText );
	              if(json.success){
	                self_.handFiles(json);
	                var outJson=[];
	                for(var i=0;i<self_.files.length;i++){
	                	var file=self_.files[i];
	                	var obj={fileId:file.fileId,fileName:file.fileName};
	                	outJson.push(obj);
	                }
	                self_.$emit("input",JSON.stringify(outJson));
	              }
	              else{
	                 self_.$dialog.alert({mes: '上传出错!'});
	              }
	            } else {
	              var msg="上传出错,状态码为:" + status;
	              self_.$dialog.alert({mes:msg});
	            }
          	});
          	
          	for(var i=0;i<fileAry.length;i++){
	          var filePath=fileAry[i];
	          var fileName=filePath.substring(filePath.lastIndexOf("/")+1)
	          task.addFile(filePath, {key:fileName} );
        	}
        	task.start();
      	},
      	
      	/**
      	 * 压缩图片
      	 */
      	
      	compressImg(fileAry){
      		var _self = this;
      		
      		//TODO 是图片就先压缩
      		for(let i = 0; i < fileAry.length; i++){
      			
      			if(fileAry[i].endsWith('.jpg')||fileAry[i].endsWith('.png')){
      				plus.zip.compressImage({
      				src: fileAry[i],
      				dst: fileAry[i].replace('.jpg','_zip.jpg'),     				
	      			},
	      			function(event) {
	      				fileAry[i] = event.target;
	      				let compressAry = [];
	      				compressAry.push(fileAry[i]);
	      				_self.upload(compressAry);	      				
	      			},
	      			function(error) {
	      			});
      			}
       			
      		}
      		
      	}
    },
    mounted() {
    	if(!this.value) {
    		this.files=[];
    		return ;
    	}
    	var fileAry=JSON.parse(this.value );
    	for(var i=0;i<fileAry.length;i++){
    		var file=fileAry[i];
    		file.selected=false;
    		this.files.push(file);
    	}
    },
    watch: {
    	files: function (val, oldVal){
    		var json=JSON.stringify(val);
			this.$emit('input', json);
		}
    }
    
}

</script>

<style scoped>

	.attacment img{
		width: 1.2rem;
		height: 1.2rem;
		display: inline-block;
		margin-left: 2px;
	}
	
	.attacment{
		width: 4rem;
		height: 1.2rem;
		display: flex;
	}
	
	.attacment > div:nth-of-type(1){
	    height: 1.2rem;
	    width: auto;
	    white-space:nowrap;
	    max-width: 4rem;
	    overflow-x:auto ;
	}
	
	.attacment > div:nth-of-type(2){
		flex-grow: 1;
		height: 1.2rem;
    	width: 1rem;
	}
	
	
	
	
	div.file-container{
	    position: relative;
	    height: 1.2rem;
	    margin-left: 2px;
	    display: inline-block;
	}
	
	.file-remove{
	    position: absolute;
	    top:0rem;
	    right:0rem;
	    display: block;
	}
	
	.yd-icon-add{
	    margin-top: .16rem;
	    display: block;
	}
	
	.file-container > img{
		display: block;
		width: auto;
		height:  1.2rem;
	}
	
	.yd-icon-removefile:before{
		content: "\e612";
		font-size: 0.6rem !important;
	    color: #fff !important ;
	    font-weight: bold;
	}

	
	.file{
	    width: 1.2rem;
	    height: 1.2rem;
	    margin-left: 2px;
	}
	
	.yd-icon-video:before{
		font-size: 0.8rem;
		
	}
	
</style>


